body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-style {
  background-image: linear-gradient(yellow, green);
  width: 100%;
  position: sticky;
  margin: 0px;
}


.main-container {
    width: 100%;

}

.nav-style {
    background-image: linear-gradient(yellow, green);
    width: 100vw;
    position: sticky;
}

.carousel-image-style {
    width: 100vw;
    height: 60vh;
}
.image-style {
    width: 25vw;
    height: 100%;
    margin-left: 5px;
    border-radius: 7%;
}

.image-style:hover {
    width: 27vw;
    height: 100%;
    margin-left: 5px;
    border-radius: 15%;
    border: 3px solid rgba(250, 250, 250, 0.5);

}

.scroll-1 {
    display: flex;
    width: 100vw;
    height: 35vh;
    border-style: solid;
    border-width: 5px;
    border-color: transparent;
    border-radius: 5px;
    overflow-x: scroll;
    position: relative;
}

.scroll-1::-webkit-scrollbar {
    display: none;
}

.side-heading {
    width: 100vw;
    height: 100%;
    background-image: url('https://homebnc.com/homeimg/2018/04/20-creative-flower-decoration-ideas-homebnc.jpg');
    background-size: cover;
    position: relative;
    border-radius: 0px 25px 25px 0px;
}

.item-name {
    color: white;
    font-family: Roboto;
    font-size: 20px;
    text-align: center;
    height: 10%;
}

.hole-container-details {
    width: 100vw;

    background-color: blue;
    padding: 20px;
}

.carrat-image {
    width: 100%;
    height: 100%;
    border-width: 4px;
    border-color: red;
    border-radius: 20px;

}

.product-name-style {
    font-family: Roboto;
    font-size: 30px;
    font-style: italic;
    font-weight: bold;
    color: orange;
}

.detailmattercontainer-style {

    background-color: white;
    border-radius: 10px;
    border-color: red;
    border-width: 4px;
}

.videocontainer-style {
    width: 100vw;
    height: 50%;
}

.mattercontainer-name-heading {
    font-family: Roboto;
    font-size: 25px;
    font-weight: bold;
    color: #7f1eb3;

}

.amount-heading {
    font-family: Roboto;
    font-size: 25px;
    font-weight: bold;
    color: #7f1eb3;
}

.label-text {
    color: #7f1eb3;
    font-family: Roboto;
    font-weight: bold;
    font-size: 20px;
}

.button {
    margin-left: 50%;
}

.credit-card {
    font-size: 40px;
}

.payment-card-img {
    width: 50px;
    height: 50px;
    margin-right: 30px;

}

.detail-side-image-style {
    width: 200px;
    height: 200px;
    background-color: transparent;
}

.input-style {
    width: 100px;
}

.home-button {
    border-radius: 5px;
    margin-top: 8px;

}
.hidden{
    display: none;
}



.sec-image{
    margin: 2%;
    width: 90%;
    height: 90%;
    border-radius: 2%;
    justify-content: center;
    
}
.container{
     background-color: white;
     margin-top: 1px;
     padding: 2px;

}
.photo-nd-desc{
    border: 1px solid rgba(0, 0, 0, 0.2);
   box-sizing: border-box;
   border-radius: 2%;
   padding: 2px;
   box-shadow: 2px 3px 2px skyblue ;
   display: grid;
}
.desc{
    display: grid;
   padding-bottom: 2%;
}
.sec-sub1{
     overflow-y: scroll;
      height: 95vh;
}
.sec-sub2{
   border: 1px solid rgba(0, 0, 0, 0.2);
   box-sizing: border-box;
   border-radius: 1%;
   padding: 2px;
   box-shadow: 2px 3px 2px skyblue ;
   height: 100vh;
}
.addToCart-btn{
    background-color: rgba(255, 0, 128, 0.9);
    color: white;
    font-family: sans-serif;
    font-weight: bold;
    font-style: inherit;
    border-radius: 3px;
    width: 40%;
    height: 40%;
}
.buyNow-btn{
    background-color: rgba(0, 255, 100, 1.0);
    color: white;
    font-family: sans-serif;
    font-weight: bold;
    font-style: inherit;
    border-radius: 3px;
    width: 40%;
    height: 40%;
}
.notification{
            position: absolute;
           font-size: 20px;
           border-radius: 50%;
           background: rgb(210, 0, 120);
           color: white;
           top: -15px;
}
.cart-opt{
      position: relative;
}
.cart-container{
     background-color: white;
     margin-top: 0px;
     padding: 2px;

}

.cart-photo-nd-desc{
    border: 1px solid rgba(0, 0, 0, 0.2);
   box-sizing: border-box;
   border-radius: 2%;
   padding: 2px;
   box-shadow: 2px 3px 2px skyblue ;
   display: grid;
}
.cart-desc{
    
}
.cart-sec-sub1{
overflow-y: scroll;
      height: 95vh;
}

.cart-sec-sub2{
   border: 1px solid rgba(0, 0, 0, 0.2);
   box-sizing: border-box;
   border-radius: 1%;
   padding: 2px;
   box-shadow: 2px 3px 2px skyblue ;
   height: 100vh;
}
.title-cart{
position: relative;
}
.remove-btn{
   position: absolute;
   top: 10%;
   right: 2%;
   font-size: 35px;
   color: white;
   border-radius: 10%;
   background-color: deeppink;
   border: none;
}
#calc-price-table{
    width: 90%;
}

.login-box{
    height: 420px;
    width: 340px;
    position: absolute;
    background-color: #17151566;
    top: 12%;
    left: 37%;
    border-radius: 5px;
    padding: 70px 30px;
    box-sizing: border-box;

}
.login-head{
   position: relative;
}
.login-close{
   position: absolute;
   right: -10%;
   top: -145%;
   border: none;
   outline: none;
   border-radius: 5px;
   background: transparent;
   color: white;
}
.login{
    text-align: center;
    color: white;
    margin: 0;
    padding: 0 0 20px;
    font-size: 22px;
    font-weight: bold;
}
.login-input{
    width: 100%;
    height: 20px;
    margin-bottom: 20px; 
    background: transparent;
    color: white;
    outline: none;
    border: none;
    border-bottom: 1px solid white;
}
p{
    color: white;
    font-weight: bold;
}
.login-btn{
    
    background-color: rgba(0, 100, 250, .5);
    width: 100px;
    height: 35px;
    font-size: 15px;
    color: white;
    border-radius: 10px;
    cursor: pointer;
}
.forgot{
    text-decoration: none;
    color: rgba(0, 100, 250);
    cursor: pointer;
}